.options {
  background-color: #101012;
  padding-top: 39px;
  padding-bottom: 6px;
  &__bloks {
    display: flex;
    justify-content: end;
    align-items: end;
  }
  &__left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 51px;
  }
  &__product {
    display: flex;
    align-items: end;
    justify-content: space-between;
    margin-bottom: 77px;
    &-imgs {
      position: relative;
      margin-right: 62px;
      &::after {
        content: '';
        height: 50px;
        top: -9px;
        width: 1px;
        background-color: #ffffff;
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
        margin: auto 2px auto 30px;
        position: absolute;
      }
    }
    &-sale {
      color: #ffffff;
      font-family: 'segoe-ui', sans-serif;
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      letter-spacing: normal;
      line-height: normal;
      text-align: left;
    }
  }
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &-title {
      color: #ffffff;
      font-family: 'segoe-ui-semibold', sans-serif;
      font-size: 20px;
      font-weight: 700;
      text-align: left;
      font-style: normal;
      letter-spacing: 0.9px;
      line-height: normal;
      margin-bottom: 7px;
      span {
        color: #ffc000;
        font-family: 'segoe-ui', sans-serif;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
      }
    }
    &-one {
      display: none;
    }
  }
  &__price {
    display: flex;
    align-items: baseline;
    justify-content: center;
    margin-bottom: 20px;
    del {
      color: #ffffff;
      font-family: 'segoe-ui', sans-serif;
      font-size: 20px;
      font-weight: 400;
      text-align: left;
      text-decoration: line-through;
      font-style: normal;
      letter-spacing: normal;
      line-height: normal;
      margin-right: 20px;
    }
    &-left {
      color: #e8af06;
      font-family: 'segoe-ui-semibold', sans-serif;
      font-size: 40px;
      font-weight: 700;
      text-align: left;
      font-style: normal;
      letter-spacing: 2px;
      line-height: 35px;
      span {
        font-family: 'segoe-ui', sans-serif;
        font-style: normal;
        letter-spacing: normal;
        line-height: 55px;
      }
    }
  }
  &__form-modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__input {
    width: 318px;
    height: 50px;
    border-radius: 10px;
    background-color: #f0f0f0;
    margin-bottom: 20px;
    border: none;
    padding-left: 19px;
    &::placeholder {
      color: #101012;
      font-family: 'segoe-ui', sans-serif;
      font-size: 18px;
      font-weight: 400;
      text-align: left;
      font-style: normal;
      letter-spacing: normal;
      line-height: normal;
    }
  }
  &__btn {
    max-width: 318px;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    background-color: #a14ab2;
    color: #ffffff;
    font-family: 'segoe-ui-semibold', sans-serif;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    margin-bottom: 39px;
    border: none;
  }
  &__sale {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__text-t {
    max-width: 188px;
    width: 100%;
    color: #f0f0f0;
    font-family: 'segoe-ui', sans-serif;
    font-size: 30px;
    font-weight: 400;
    text-align: center;
    font-style: normal;
    letter-spacing: normal;
    line-height: 37px;
    margin-bottom: 4px;
  }
  &__text-s {
    color: #ffc000;
    font-family: 'segoe-ui-semibold', sans-serif;
    font-size: 60px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: 3px;
    line-height: 50px;
    text-align: left;

  }
  &__footer {
    padding-top: 36px;
    padding-bottom: 57px;
  }
  &__imge {
    display: none;
  }

}
