.thanks {
  // background-image: url('../img/thnks-bgs.jpg');
  // background-repeat: no-repeat;
  // background-size: 100% 100%;
  // -webkit-box-align: center;
  // -ms-flex-align: center;
  // align-items: center;
  // position: relative;
  display: flex;
  justify-content: center;
  background-position: right;
  align-items: center;
  height: 100vh;
  // padding-top: 285px;
  // padding-bottom: 223px;
  &__block {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &-top {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-bottom: 112px;
      position: relative;
    }

  }
  &__imgs {
    margin-bottom: 21px;
  }
  &__title {
    font-family: 'Open Sans', sans-serif;
    font-size: 50px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 61px;
    text-align: center;
    /* Text style for "ПОЗДРАВЛЯЕ" */
    color: #2a2a2a;
    max-width: 542px;
    width: 100%;
    span {
      color: #224856;
    }
  }
  &__text {
    color: #2a2a2a;
    font-family: 'Open Sans', sans-serif;
    font-size: 26px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 35px;
    text-align: center;
    /* Text style for "Наш менедж" */
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    max-width: 584px;
    width: 100%;
  }
  &__bg {
    position: relative;
  }
  &__container {
    position: absolute;
  }
  &__bgs {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }



}
.thanks__block-top {
  position: relative;
  &::after {
    content: '';
    position: absolute;
    width: 200px;
    height: 1px;
    background-color: #052f50;
    bottom: -55px;
  }
}
