.price {
  background-color: #020002;
  width: 100%;
  padding: 20px 0 15px 0;
  &__bloks {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__options{
    margin-right: 67px;
    del {
      color: #8e8e8e;
    }
  }
  &__btn {
    max-width: 318px;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    background-color: #ffc000;
    color: #000000;
    font-family: 'segoe-ui-semibold', sans-serif;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    border: none;
  }

}
