*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
  box-sizing: border-box;
  background: #FFFFFF;
}
body {
  font-family: 'segoe-ui', sans-serif;
}
img {
  max-width: 100%;
}

*:active, *:hover, *:focus{
  outline: 0;
  outline-offset: 0;
}

a{
  text-decoration: none;
  color: inherit;
}

ul,
li{
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p{
  margin: 0;
}

button, a {
  cursor: pointer;
}

