.complect {
  background-image: url('../img/complect.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  height: 849px;
  padding-top: 64px;
  &__title {
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 25px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 69px;
    span {
      font-family: 'segoe-ui-semibold', sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: normal;
      line-height: normal;
    }
  }
  &__list {
    margin-left: 57px;
    margin-bottom: 210px;
  }
  &__item {
    position: relative;
    padding-left: 49px;
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 40px;
    text-align: left;
    &::before {
      content: "";
      background-color: #ffc000;
      width: 30px;
      height: 1px;
      position: absolute;
      top: 20px;
      left: 0;
    }
  }
  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin-right: 108px;
    &-text {
      color: #101012;
      font-family: 'segoe-ui', sans-serif;
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      letter-spacing: normal;
      line-height: 25px;
      text-align: left;
      text-transform: uppercase;
      max-width: 571px;
      width: 100%;
      margin-right: 34px;
      margin-bottom: 23px;
      span {
        font-family: 'segoe-ui-semibold', sans-serif;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
      }
    }
  }
  &__form {
    &-modal {
      display: flex;
      align-items: end;
      justify-content: center;
      max-width: 620px;
      width: 100%;
    }
  }
  &__input {
    width: 220px;
    height: 50px;
    border-radius: 10px;
    background-color: #f0f0f0;
    margin-bottom: 20px;
    border: 0;
    padding-left: 19px;
    margin-right: 10px;
    &::placeholder {
      color: #101012;
      font-family: 'segoe-ui', sans-serif;
      font-size: 18px;
      font-weight: 400;
      text-align: left;
      font-style: normal;
      letter-spacing: normal;
      line-height: normal;
    }
  }
  &__btn {
    max-width: 150px;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    background-color: #a14ab2;
    color: #ffffff;
    font-family: 'segoe-ui-semibold',sans-serif;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    border: 0;
  }

}
