.example  {
  background-image: url('../img/desc-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
  height: 671px;
  display: flex;
  align-items: center;
  position: relative;
  &__bloks {
    display: flex;
    align-items: end;
    justify-content: center;
    max-width: 713px;
    width: 100%;
  }
  &__cards {
    display: flex;
    align-items: end;
    justify-content: space-between;
    flex-direction: column;
    &-right {
      margin-right: 95px;
    }
  }
  &__card {
    display: flex;
    align-items: end;
    justify-content: center;
    margin-bottom: 44px;
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
  }
  &__img {
    margin-right: 9px;
  }
  &__text {
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 22px;
    text-align: left;
    max-width: 218px;
    width: 100%;
    // &-four {
    //   max-width: 200px;
    // }
    &-five {
      max-width: 281px;
    }
    // &-six {
    //   max-width: 221px;
    // }
  }

}


