.footer {
  padding-top: 36px;
  padding-bottom: 20px;
  &__bloks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__text {
    max-width: 329px;
    width: 100%;
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
    margin-bottom: 13px;
  }
  &__link {
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    text-align: center;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }


}
