    font-size: 14px;
  }


}

@media screen and ( max-width:850px ) {
  .complect__item {
    font-size: 13px;
  }

}


@media screen and ( max-width:768px ) {
  .example {
    height: 100%;
    padding-top: 65px;
    padding-bottom: 100px;
  }
  .burger {
    display: block;
  }
  .menu  {
    display: block;
    position: fixed;
    left: -150vw;
    top: 0;
    width: 304px;
    height: 100%;
    z-index: 120;
    background-color: rgba(0, 0, 0, 0.8);
    transition: left ease 0.5s;
    flex-direction: column;
  }

  .menu--visible {
    left: 0;
    transition: left  ease 0.5s;
  }
  .menu__close {
    width: 37px;
    height: 37px;
    position: absolute;
    right: 50%;
    left: 44%;
    top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: transparent;
    background-color: transparent;
    border: none;

  }

  .menu__close-line {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    background-color: #747474;
  }

  .menu__close-line:first-child {
    transform: rotate(45deg);
  }

  .menu__close-line:last-child {
    transform: rotate(-45deg);
  }
  .burger__header_menu {
    display: block !important;
  }
  .logo__menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 75px;
    margin-top: 100px;
  }
  .logo__items {
    padding-right: 0;
    font-size: 16px;
    color: #ffffff;
    margin-bottom: 40px;
  }
  .header__left-one-burger {
    position: relative;
  }
  .scroll-cards {
    overflow-y: scroll;
    overflow-y: auto;
    height: 100%;
  }
  .scroll-cards::-webkit-scrollbar {
    width: 0;
  }
  .header__left-one-burger::after {
    top: 1px;
  }
  .header__left-top-burger {
    justify-content: center;
  }
  .header__autor-numb-burger {
    font-size: 15px;
  }

  .header_menu {
    display: none;
  }
  .options {
    padding-top: 0;
    &__footer {
      padding-top: 64px;
    }
    &__form {
      display: none;
      &-one {
        display: block;
        order: -1;
        position: relative;
        top: -50px;

      }
      &-title {
        text-align: center;
      }
    }
    &__bloks {
      flex-direction: column-reverse;
      align-items: center;
    }
    &__product {
      flex-direction: column;
      margin-bottom: 0;
      align-items: center;
      &-imgs {
        margin-right: 0;
        width: 160px;
        height: auto;
        &::after {
          display: none;
        }
      }
      &-sale {
        font-size: 16px;
      }
    }
    &__left {
      padding-top: 20px;
    }
    &__input {
      width: 300px;
    }
    &__btn {
      width: 300px;
    }
  }
  .example {
    &__cards-right {
      margin-right: 0;
    }
    &__card {
      margin-bottom: 20px;
      &:nth-last-child(1) {
        margin-bottom: 20px;
      }
    }
    &__bloks {
      flex-direction: column;
    }
    &__text {
      font-size: 15px;
    }
  }
  .details {
    height: 100%;
    &__imgs {
      width: 100%;
    }
    &__title {
      font-size: 22px;
    }
    &__btn {
      display: block;
    }
    &__card {
      flex-direction: column;
    }
    &__title {
      margin-bottom: 41px;
    }
    &__text-l {
      margin-right: 0;
      text-align: left;
      max-width: 100%;
    }
    &__text-l {
      &::before {
        display: none;
      }
    }

  }
  .price {
    &__bloks {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &__options{
      margin-right: 0;
    }
  }
  .complect {
    background-image: url(../img/complecte.jpg);
    background-position: bottom;
    background-size: auto;
    height: 100%;
    padding-top: 40px;
    &__title {
      font-size: 23px;
      margin-bottom: 17px;
    }
    &__item {
      font-size: 15px;
    }
    &__form-modal {
      flex-direction: column;
      align-items: center;

    }
    &__bottom-text {
      margin-right: 0;
    }
    &__bottom {
      padding-bottom: 275px;
      position: relative;
      align-items: center;
      &::before {
        content: '';
        position: absolute;
        background-image: url('../img/mobail.png');
        background-repeat: no-repeat;
        background-size: contain;
        max-width: 258px;
        height: 249px;
        margin: auto;
        right: 0;
        left: 0;
        z-index: 9;
        bottom: 0;
      }
      &-text {
        max-width: 294px;
        margin-bottom: 19px;
      }

    }
    &__input {
      width: 300px;
      margin-right: 0;
      margin-bottom: 10px;
    }
    &__btn {
      max-width: 300px;
    }
    &__list {
      position: relative;
      margin-bottom: 258px;
      &::before {
        content: '';
        position: absolute;
        background-image: url('../img/detail.png');
        background-repeat: no-repeat;
        background-size: contain;
        max-width: 282px;
        height: 178px;
        margin: auto;
        right: 0;
        left: 0;
        z-index: 9;
        bottom: -204px;
      }

    }
  }
  .slids {
    padding-top: 42px;
    &__title {
      font-size: 23px;
      line-height: 33px;
      margin-bottom: 27px;
    }
    &__block {
      flex-direction: column;
      align-items: center;
      &-left {
        margin-right: 0;
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
        &::after {
          display: none;
        }
      }
    }
    &__text {
      text-align: center;
    }
    &__partners {
      margin-top: 114px !important;
      padding: 37px 10px 36px 10px;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: 0;
      &-imgs {
        margin-right: 0;
        max-width: 207px;
        height: 100%;
        margin-bottom: 30px;
      }
      &-textb {
        text-align: center;
      }
      &-textt {
        text-align: center;
      }
    }
    &__partners-one {
      display: flex;
      align-items: center;
    }
    &__partners-ones {
      display: none;
    }

  }

}
