.slids {
  padding-top: 89px;
  &__title {
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 25px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 62px;
    span {
      font-family: 'segoe-ui-semibold', sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: 1px;
      line-height: normal;
    }
  }
  &__block {
    display: flex;
    align-items: end;
    justify-content: center;
    &-left {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      margin-right: 60px;
      position: relative;
      &::after {
        content: '';
        height: 160px;
        top: -14px;
        width: 1px;
        background-color: #adb0b5;
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
        margin: auto -31px auto 23px;
        position: absolute;
      }
    }
  }
  &__text {
    max-width: 760px;
    width: 100%;
    color: #6b6b6b;
    font-family: 'segoe-ui', sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 23px;
    text-align: left;
  }
  &__imgs {
    margin-bottom: 8px;
  }
  &__name {
    margin-bottom: 7px;
    color: #101012;
    font-family: 'segoe-ui-semibold', sans-serif;
    font-size: 18px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 25px;
    text-align: right;
    text-transform: uppercase;
  }
  &__partners {
    margin-top: 149px !important;
    margin-bottom: 93px !important;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 38px 83px 36px 81px;
    border-radius: 10px;
    border: 2px solid #c3151c;
    max-width: 1026px;
    width: 100%;
    margin: 0 auto;
    &-imgs {
      margin-right: 73px;
    }
    &-top {
      margin-bottom: 25px;
    }
    &-textt {
      color: #101012;
      font-family: 'segoe-ui-semibold', sans-serif;
      font-size: 21px;
      font-weight: 700;
      font-style: normal;
      letter-spacing: normal;
      line-height: 25px;
      text-align: left;
      margin-bottom: 2px;
    }
    &-textb {
      color: #101012;
      font-family: 'segoe-ui', sans-serif;
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      letter-spacing: normal;
      line-height: 25px;
      text-align: left;
    }
  }
  &__partners-one {
    display: none;
  }

}






/* arrows */

.slider__button {
  display: none;
}

.slider-left {
  display: none;
}

.slider-right {
  display: none;
}

.slick-disabled {
  pointer-events:none;
}
.slick-arrow {
  display: none !important;
}


/* dots */

.slick-dots {
  position: absolute;
  list-style: none;
  transform: translateX(-50%);
  left: 50%;
  margin-top: 50px;
  display: flex;
  align-items: center;
}

.slick-dots li {
  width: 5px;
  height: 5px;
  background-color: #c2c9d6;
  border-radius: 50%;
  margin:0 16px;
  transition: all .4s;
}

li.slick-active {
  width: 9px;
  height: 9px;
  border-radius: 5px;
  background-color: #5a77ac;
}

.slick-dots button {
 display: none;
}
