.details {
  background: url('../img/details-s.jpg');
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #eef1f6;
  height: 1600px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  position: relative;

  &__bloks {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: -93px;
  }
  &__title {
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 25px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 56px;
    span {
      font-size: 30px;
      font-family: 'segoe-ui-semibold', sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: 1.1px;
      line-height: 25px;
    }
  }
  &__cards {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-left: 278px;
  }
  &__card {
    display: flex;
    align-items: end;
    justify-content: center;
    margin-bottom: 37px;
    max-width: 850px;
    width: 100%;
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
  }
  &__text-l {
    color: #101012;
    font-family: 'segoe-ui-semibold', sans-serif;
    font-size: 18px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 25px;
    text-align: right;
    text-transform: uppercase;
    max-width: 131px;
    width: 100%;
    margin-right: 60px;
    position: relative;
    &::before {
      content: '';
      height: 86px;
      top: -14px;
      width: 1px;
      right: -30px;
      background-color: #adb0b5;
      -webkit-transform: rotate(180deg);
      transform: rotate(180deg);
      position: absolute;
    }
  }
  &__text-t {
    max-width: 544px;
    width: 100%;
    color: #101012;
    font-family: 'segoe-ui', sans-serif;
    font-size: 15px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 23px;
    text-align: left;
    margin-bottom: 8px;
  }
  &__text-b {
    color: #fec414;
    font-family: 'segoe-ui', sans-serif;
    font-size: 13px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 23px;
    text-align: left;
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    &::before {
      content: '';
      position: absolute;
      background-image: url('../img/next.png');
      width: 25px;
      height: 13px;
      left: 101px;
    }
  }
  &__btn {
    display: none;
    background-color: #ffc000;
    color: #000;
    margin-bottom: 36px;
  }
  &__imgs {
    cursor: pointer;
    position: relative;
    z-index: 1;

  }
}


.details__text-a {
  display: none;
  max-width: 646px;
  width: 100%;
  color: #101012;
  font-family: 'segoe-ui', sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 23px;
  text-align: left;
  margin-bottom: 8px;

}
.details__text-right.details__text-right--active .details__text-a {
  display: block;
}
.details__text-right.details__text-right--active .details__text-t {
  margin-bottom: 0;
}
// .details__title + .details__text-right.details__text-right--active  {
//   margin-bottom: 6px !important;
// }
.details__bloks + .details__cards + .details__card + .details__text-right.details__text-right--active > .details__title {
  margin-bottom: 6px !important;
}

.details__text-tt {
  display: none;
}
.details__text-aa {
  display: none;
}


.hidden{
  display: none;
}

